<template>
	<section id="works">
		<article class="ui-masonry">
			<div class="title">
				<h4>WORKS</h4>
				<div class="desc">
					<p>국내 최고 금융 기관을 대상으로 차별화된 UX/UI 를 구현하고 있습니다.</p>
				</div>
			</div>
			<masonry-wall :items="projects" :ssr-columns="1" :column-width="300" :gap="16" :class="{'on-stage':onStage}" class="masonry-grid">
				<template #default="{item, index}">
					<div class="work-itm" v-on:click="toggleModal(item, true)">
						<div class="thumb"><img :src="require(`@/assets/img/img_portfolio_${ item.nick }.png`)"></div>
						<div class="title">
							<!-- <h5><button class="lbl" v-html="item.title" v-on:click="item.modal = true"></button></h5> -->
							<h5><button class="lbl" v-html="item.title"></button></h5>
							<button class="btn-remove" v-on:click="removeItem(index)"><span class="icn"></span><span class="lbl clipped">지워보기</span></button>
						</div>
					</div>
				</template>
			</masonry-wall>
		</article>
	</section>
	<section class="ui-modal">
		<div v-for="(project, i) in projects" :key="i" class="">
			<transition name="fade">
				<div class="dialog-bg" v-if="project.modal">
					<WorksModal :param="project" @close="toggleModal(project, false)"></WorksModal>
				</div>
			</transition>
		</div>
	</section>
</template>

<script>
import dataWork from '@/assets/data/work.js'
import WorksModal from '@/components/WorksModal.vue'

export default {
	name: 'Works',
	components: {
		WorksModal
	},
	
	// data: function () {
	// 	return {
	// 		projects: dataWork
	// 	}
	// },
	data: () => ({
		projects: dataWork,
		html : document.querySelector("html"),
		onStage: false
	}),
	methods: {		
		removeItem(idx) {
			this.projects.splice(idx, 1);
			this.projects = [...this.projects]
		},	
		scrollListener: function (e) {
			if (!this.onStage) this.onStage = window.scrollY > 300;
		},
		// onModal(obj) {
		// 	obj.modal = true;
		// 	this.html.classList.add("on-modal");
		// },
		// offModal(obj){
		// 	obj.modal = false;			
		// 	this.html.classList.remove("on-modal");			
		// },
		toggleModal(obj, state) {
			if (state) {
				this.html.classList.add("on-modal");
			} else {		
				this.html.classList.remove("on-modal");
			}
			obj.modal = state;
		}
	},
	mounted() {
		// this.setWork();
		// this.setProject();
		console.log("mounted: Works");
		console.log( "$(\".masonry-wall\").offset().top", $(".masonry-wall").offset().top )
		window.addEventListener('scroll', this.scrollListener)
	},
	setup() {
		// let projects = dataWork
		let modalAlert = () => {
			alert("modalAlert");
		};
		return {
			// projects,
			modalAlert
		}
	}
}
</script>

