	/* content:"\f0f6"; */
	content:"\f15c";
}

.dialog-bg{display:flex;justify-content:center;align-items:center;position:fixed;top:0;right:0;bottom:0;left:0;background-color:rgba(0, 0, 0, .6);transition:opacity .5s;}
