<style>
/* works */
.ui-masonry .masonry-item {position: relative;}
.ui-masonry .masonry-item img {width: 100%;transition: all 1.2s ease;}
.ui-masonry .masonry-item .thumb {position: relative;}
.ui-masonry .masonry-item .title {
	display: flex;justify-content:center;align-items: center;position: absolute;bottom: 0;left: 0;width: 100%;
	padding: 8px 16px 12px 16px;background-color: rgba(0, 0, 0, .8); height: 60px;
	z-index: 1; color: #fff;transition:all .3s ease;
}
.ui-masonry .masonry-item .btn-remove {display: none;}
.ui-masonry .masonry-item .btn-remove .icn {width: 24px;height: 24px;text-align: center;color: rgba(255, 255, 255, 1);}
.ui-masonry .masonry-item .btn-remove .icn:after {
	line-height: 1.3;
	font-family: FontAwesome;
	font-size: 22px;
	font-weight: normal;
	font-style: normal;
	-webkit-font-smoothing: antialiased;
	content: "\f014";
}


.masonry-grid .masonry-item{border-radius:16px;overflow:hidden;}
.masonry-grid .masonry-item .work-itm{position:relative;}
.masonry-grid .masonry-item .work-itm .thumb,
.masonry-grid .masonry-item .work-itm .title{opacity:0;}
.masonry-grid .masonry-item .work-itm .thumb{position:relative;transform:translateY(120px);}
.masonry-grid.on-stage .masonry-item .work-itm .thumb{
	animation:fadeIn .4s .2s ease both, slideUp .4s .2s ease both;
}
.masonry-grid.on-stage .masonry-item .work-itm .title{animation:fadeIn .4s .4s ease both}
@keyframes fadeIn {
	0% {opacity:0;}
	100% {opacity:1;}
}
@keyframes slideUp {
	0% {transform:translateY(120px);}
	100% {transform:translateY(0);}
}
.masonry-column:nth-child(1) .masonry-item:nth-child(3n-2) .thumb,
.masonry-column:nth-child(2) .masonry-item:nth-child(3n-1) .thumb,
.masonry-column:nth-child(3) .masonry-item:nth-child(3n-0) .thumb {animation-delay:.2s;}
.masonry-column:nth-child(1) .masonry-item:nth-child(3n-1) .thumb,
.masonry-column:nth-child(2) .masonry-item:nth-child(3n-0) .thumb,
.masonry-column:nth-child(3) .masonry-item:nth-child(3n-2) .thumb {animation-delay:.4s;}
.masonry-column:nth-child(1) .masonry-item:nth-child(3n-0) .thumb,
.masonry-column:nth-child(2) .masonry-item:nth-child(3n-2) .thumb,
.masonry-column:nth-child(3) .masonry-item:nth-child(3n-1) .thumb {animation-delay:.6s;}
