<template>
	<header>
		<section>
			<article>
				<!-- <h1><img src="@/assets/img/logo_header.png" alt="PIINOXD"></h1> -->
				<!--// (case 2)
				<h1><img :src="require(`@/assets/img/${logo}`)" alt="PIINOXD"></h1>
				//-->
				<h1><img :src="logo.piinouxd" alt="PIINOXD"></h1>
				<!-- <h1><img :src="logo.kb" alt="KB"></h1> -->
				<nav>
					<ul>
						<li v-for="(menu,index) in  dataMenu.name" :key="index" :class="{'current': index == dataMenu.current}"><a :href="`#${menu}`" class="menu-btn" v-on:click="dataMenu.current = index">{{menu}}</a></li>
					</ul>
					<div class="blob"></div>
				</nav>
			</article>
		</section>
	</header>
</template>

<script>
import {ref, reactive} from 'vue';
export default {
	name: 'DefaultHeader',
	/* (case 2)
	data: function() {
		return {
			logo: 'logo_header.png'
		}
	}
	*/
	data: () => ({
		logo: {
			piinouxd: require('@/assets/img/logo_header.png'),
			kb: require('@/assets/img/logo_kb.png')
		}
	}),
	setup() {
		const dataMenu = reactive({
			name : ['intro', 'works', 'insta', 'contact'],
			current : 0
		});
		return{
			dataMenu
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	header {position:fixed;top:0;left:0;right:0;bottom:0;height:60px;background:rgba(0, 0, 0, .6);}
	header.on {margin:0; transition:all 1s ease-out, transform 1s; -webkit-transition:all 1s ease-out, transform 1s; -moz-transition:all 1s ease-out, transform 1s; -o-transition:all 1s ease-out, transform 1s;}
	header h1 {padding:0; line-height:60px;}
	header h1 img {vertical-align:middle;}
	header nav {position:absolute; top:0; right:0;}
	header nav:after {display:block; clear:both; content:'';}
	header nav ul {display:flex;}
	header nav li.current{background-color: green;}
	header nav li a.menu-btn {display:block; position:relative; padding:20px 30px; color:#fff; outline: 1px solid red;font-family:Verdana; font-size:18px; text-decoration: none; text-transform: uppercase;}
	header nav li a.menu-btn:hover {color:#0ff; transition: all 0.5s;}
	header nav .blob {position:absolute; left:0; bottom:10px; width: 100px; height:2px; background:#01aa95; transition:width 0.5s, transform 0.5s;}
</style>
