<!-- eslint-disable vue/valid-template-root -->
<template>
	<div class="dialog">
		<div class="detail_wrap">
			<a href="#" class="close-dialog" v-on:click.prevent="closeModal">			
				<span class="icn"></span>
				<span class="lbl clipped">닫기</span>
			</a>
			<div class="content">
				<div class="potfolio_img" v-for="(img, i) in param.imgs" :key="i">
					<img :src="require(`@/assets/img/portfolio/${img}`)">
				</div>
				<div class="side">
					<div class="title">
						<h2 v-html="param.title"></h2>
					</div>
					<div class="list">
						<dl>
							<dt><span>CLIENT</span></dt>
							<dd>{{ param.client }}</dd>
							<dt><span>CATEGORY</span></dt>
							<dd>{{ param.category }}</dd>
							<dt><span>ROLE</span></dt>
							<dd>{{ param.role }}</dd>
							<dt><span>PERIOD</span></dt>
							<dd>{{ param.period }}</dd>
						</dl>
						<div class="description">
							<p>{{ param.description }}</p>
						</div>
						<div class="strategy">
							<h6>Strategy</h6>
							<p v-html="param.strategy"></p>
						</div>
						<div class="concept">
							<h6>Concept</h6>
							<p>{{param.concept}}</p>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<!-- 0 1 10 Number -->
<!-- "abc" String -->
<!-- [1,2,3] Array -->
<!-- props: {project: String} Object -->
<script>

export default {
	name: 'WorksModal',
	components: {
	},
	props: {
		param: Object
	},
	data: () => ({
		html : document.querySelector("html")
	}),
	methods: {	
		toggleModal(obj, state) {
			obj.modal = state;
			//jquery: $("html").toggleClass("on-modal", state);
			if (state) {
				this.html.classList.add("on-modal");
			} else {		
				this.html.classList.remove("on-modal");
			}
		}
	},
	mounted() {
	
	},
	setup(props, context) {		
		const closeModal = () => {
			context.emit("close", true)
		};	
		return {
			closeModal
		};
	}
}
</script>


<style scoped>
/* dialog */	
.dialog{width:100%;height:100%;padding:20px;background:rgba(255, 255, 255, 1);overflow: scroll;}
.dialog .title{display:flex;justify-content:space-between;}
.dialog .content h6 + p{margin-top:8px;}
.dialog .content p + h6{margin-top:16px;}
.dialog .close-dialog {position:absolute; top:30px; right:30px; width:33px; height:33px; border:0 none;}
.dialog .close-dialog .icn{width:33px;height:33px;text-align:center;color:rgba(0, 0, 0, 1);}
.dialog .close-dialog .icn:after{
	content: '';
	width: 33px;
	height: 33px;
	display: block;
	background: url(@/assets/img/btn/btn_layerpop.png) no-repeat 0 -50px;
}
.dialog .close-dialog .icn:hover {
	animation: rotate 1.5s infinite linear;
}
@keyframes rotate {
	from {
		transform: rotate(0deg);
		-webkit-transform: rotate(0deg);
		transform-origin: 16.5px 16.5px;
	}

	to {
		transform: rotate(360deg);
		-webkit-transform: rotate(360deg);
		transform-origin: 16.5px 16.5px;
	}
}
.dialog .detail_wrap {position: relative;width: 1280px;margin: 0 auto;padding: 100px 30px;}
.dialog .detail_wrap .content .portfolio_img {overflow: hidden; width: 730px;}
.dialog .detail_wrap .content .portfolio_img img {width: 100%; }
.dialog .detail_wrap .content .portfolio_img:first-child img {margin-top: 0;}
.dialog .detail_wrap .content .side {overflow: hidden;position: absolute;top: 100px;	right: 30px;width: 440px;}
.dialog .detail_wrap .content .side .title {padding:15px 0 60px 0;}
.dialog .detail_wrap .content .side .title h2{font-size: 34px; color: #333; letter-spacing: -1px;}
.dialog .detail_wrap .content .side .list {}
.dialog .detail_wrap .content .side .list dl {
	display: block;
	width: 100%;
	border-top: 1px solid #e5e5e5;
	font-size: 0;
}

.dialog .content .side .list dt,
.dialog .content .side .list dd {
	display: inline-block;
	padding: 25px 0;
	border-bottom: 1px solid #e5e5e5;
	font-size: 16px;
}
.dialog .content .side .list dt {
	width: 125px;
	color: #111;
}
.dialog .content .side .list dd {
	width: 315px;
	color: #888;
}
.dialog .content .description {margin-top: 60px;}
.dialog .content .description p {color: #666;font-size:16px;line-height:24px;}
.dialog .content .strategy {margin-top: 55px;}
.dialog .content .strategy h6 , .dialog .content .concept h6 {display: block;margin-bottom: 15px;color:#111;font-size: 18px;}
.dialog .content .strategy p {color: #666;font-size: 16px;line-height:24px;}
.dialog .content .concept{margin-top: 55px;}
</style>
