<!-- <transition name="fade">
  <a v-on:click="ScrollUp" v-show="visible" class="scrollup"></a>
</transition> -->

<!-- 토글클래스 -->
<template>
  <a v-on:click="ScrollUp" :class="{'is-visable':isVisable}" class="scrollup" ></a>
</template>

<script>
export default {
  data () {
    return {
      isVisable: false
    }
  },
  methods: {
    ScrollUp: function () {
		window.scrollTo(0, 0);
    },
    scrollListener: function (e) {
      this.isVisable = window.scrollY > 250;
    }
  },
  mounted: function () {
    window.addEventListener('scroll', this.scrollListener)
  },
// beforeUnmount: function () {
//     window.removeEventListener('scroll', this.scrollListener)
//     console.log("beforeUnmount");
//   }
}
</script>

<style scoped>
.scrollup{opacity: 0; cursor: pointer; position: fixed; width: 130px; height: 130px; right: 80px; bottom: 0px; border-radius: 50%; background: url(@/assets/img/scrolltop.png) no-repeat -72px -2px; background-size: cover; transition: all 0.6s ease; }
.scrollup.is-visable{opacity: 1; transition: all 0.6s ease; bottom: 40px;}


